import React, { Component } from 'react'
import { Platform, Text, View, ActivityIndicator } from 'react-native'
import { Input } from 'react-native-elements'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux';
import NavigationService from '../../../Services/NavigationService'

import FirebaseActions from '../../../Stores/Firebase/Actions'
import { Actions, DataRefs } from '../../../Stores/Firebase/Selectors'

import { Button } from '../../../Components'
import Style from './AuthStyle'
import { ApplicationStyles, Helpers, Metrics } from '../../../Theme'

import { UserStates, DataStringCheck } from '../../../Stores/Firebase/Selectors'

class CreateNewUserData extends Component{
  constructor(props){
    super(props);
    this.state = {
      Farm: "Test",
      Sections: 1,
      Pens: 1
    }
  }

  getData() {
  }

  checkData(props) {
  }

  componentDidMount() {
    this.props.setViewLoading(false)
    this.props.setViewError(null)
    this.checkData(this.props);
    this.getData();
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps) {
      this.checkData(this.props);
      if (this.props.firebase.viewError != prevProps.firebase.viewError) {
        this.forceUpdate()
      }
      if (this.props.firebase.firebaseUser !== prevProps.firebase.firebaseUser) {
        if (prevProps.firebase.firebaseUser.state !== UserStates.STARTER) {
          NavigationService.nacigationPop()
        }
      }
    }
  }

  render() {
    const IsLoading = this.props.firebase.firebaseIsLoading
    const ErrorMessage = this.props.firebase.viewError

    return(
      <View
        style={[
          Helpers.mainContainer,
        ]}
      >
        <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fillColMain]}>
          {ErrorMessage ? (
            <Text style={ApplicationStyles.error}>{ErrorMessage}</Text>
          ):(<></>)}

          <View style={ [Helpers.borderContainer, Helpers.midWidth, {marginBottom:20}] }>
            <Input
              label="Farm Name"
              style={ApplicationStyles.input}
              onChangeText={text => {
                if (text){
                  if (DataStringCheck(text)){
                    this.setState({Farm:text})
                  }
                }else {
                  this.setState({Farm:text})
                }
              }}
              value={this.state.Farm}
            />
            <Input
              label="Number of Sections"
              style={ApplicationStyles.input}
              onChangeText={text => {
                if (!isNaN(text)){
                  this.setState({
                    Sections:text
                  })
                }
              }}
              value={this.state.Sections}
            />
            <Input
              label="Number of Pens in Sections"
              style={ApplicationStyles.input}
              onChangeText={text => {
                if (!isNaN(text)){
                  this.setState({
                    Pens:text
                  })
                }
              }}
              value={this.state.Pens}
            />
          </View>
          <View style={[Helpers.buttonContainer, Helpers.midWidth] }>
            <Button Text="Create User" 
              onPress={() => {
                var _UserData = {}
                var _sections = {};
                var _pens = {};

                if (!this.state.Farm || this.state.Farm === ""){
                  this.props.setViewError("Farm name is needed")
                }
                else if (this.state.Sections && isNaN(this.state.Sections)){
                  this.props.setViewError("Number of Sections have to be a number")
                } else if (this.state.Pens && isNaN(this.state.Pens)){
                  this.props.setViewError("Number of Pens in Sections have to be a number")
                }
                else {
                  _UserData = {state: UserStates.FREE, farms: {}}
                  
                  var Pen = 1;
                  var i, i2;
                  for (i = 1; i <= this.state.Sections; i++) {
                    var sectionPens = {};
                    for (i2 = 1; i2 <= this.state.Pens; i2++) {
                      _pens['Pen_' + Pen] = {section: 'Section_' + i}
                      sectionPens['Pen_' + Pen] = true
                      Pen++;
                    }
                    
                    _sections['Section_' + i] = {pens: sectionPens}
                  }
                  this.props.setViewLoading(true)
                  this.props.createUserData(_UserData, this.state.Farm, _sections, _pens)
                }
              }}/>
            </View>
        </View>
        {(IsLoading || this.props.firebase.viewIsLoading) &&
          <ActivityIndicator style={Helpers.loadingContainer} size='large' />
        }
      </View>
    )
  }
}

const mapStateToProps = (state) => {
  const { firebase } = state
  return { firebase }
};

const mapDispatchToProps  = (dispatch) => {
  return {
    setViewLoading: state => dispatch(FirebaseActions.viewIsLoading(state)),
    setViewError: error => dispatch(FirebaseActions.setViewError(error)),
    
    createUserData: (Userdata, Farm, Sections, Pens) => dispatch(FirebaseActions.actionStarting(Actions.CREATE, DataRefs.USERSDATA, {UserData: Userdata, Farms:{name:Farm}, Sections:Sections, Pens:Pens})),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(CreateNewUserData);
//export default CreateNewUserData;