export function* handleAuthState() {
  const authChannel = yield eventChannel((emit) => {
    const subscription = firebase.auth().onAuthStateChanged((user) => {
      if (user == null){
        emit("NONE")
      }else {
        emit(user)
      }
    })
    return () => {
      subscription()
    };
  }, buffers.expanding(1))

  try {
    for (;;) {
      const newAuth = yield take(authChannel)
      var firebaseUser = newAuth === "NONE"?null:newAuth;
      if (firebaseUser) {
        const userData = yield call(firebaseService.actionStarting, DataRefs.USERSDATA, Actions.FETCH, {UserId: firebaseUser.uid})
        if (userData.data) {
          firebaseUser.farms = userData.data.data.farms
          firebaseUser.state = userData.data.data.state
          yield put(FirebaseActions.dataFetchSuccess({type:Actions.UPDATE, path:DataRefs.FIREBASEUSER, data: firebaseUser}))
          yield put(FirebaseActions.dataFetchSuccess({type:Actions.UPDATE, path:DataRefs.USERSDATA, data: userData.data}))    //TODO: mabye moved to firebaseUser..
        }
      } else {
        yield put(FirebaseActions.dataFetchSuccess({type:Actions.UPDATE, path:DataRefs.FIREBASEUSER, data: {}}))
        yield put(FirebaseActions.dataFetchSuccess({type:Actions.UPDATE, path:DataRefs.USERSDATA, data: {}}))    //TODO: mabye moved to firebaseUser..
      }
    }
  } finally {
    if (yield cancelled()) {
      authChannel.close()
    }
  }
}

export function* handleActions() {
  const channel = yield actionChannel([
    'ACTION_STARTING',
  ])

  for (;;) {
    const action: ACTION_STARTING = yield take(channel)

    //TODO: mabye clean up Farmid prop.. by current firebase user.. localdata framid.. append here..
    //console.log(action.ref, action.action)

    if (Object.values(Actions).includes(action.action)) {
      if (Object.values(DataRefs).includes(action.ref)) {
        const FirebaseUser = yield select((state) => state.firebase.firebaseUser);
        if (action.action != Actions.SIGN && FirebaseUser && FirebaseUser.farms) {
          if (!action.params) action.params = {}
          if (!action.params.FarmId) action.params.FarmId = [Object.keys(FirebaseUser.farms)[0]]
        }

        const NewData = yield call(firebaseService.actionStarting, action.ref, action.action, action.params)
        if (NewData && (NewData.error || NewData.viewError)) {
          yield put(FirebaseActions.fetchFailure(NewData.error))
          yield put(FirebaseActions.setViewError(NewData.viewError))
        } else {
          //console.log("resCall: ", NewData)
          if (NewData && NewData.hasOwnProperty("type") && NewData.hasOwnProperty("path") && NewData.hasOwnProperty("data")) {
            yield put(FirebaseActions.dataFetchSuccess(NewData))
          }
          else {
            //console.log("Old Path: ", action.ref, action.action, params, NewData)
            yield put(FirebaseActions.fetchSuccess({localDataStore:NewData}))
          }
        }
      }
    }
  }
}

// Notification Handler for then App is in Foreground State
export function* onMessageHandler() {
  const onMessageChannel = yield eventChannel((emit) => {
    const messageListener = firebase.messaging().onMessage((remoteMessage) => {
      //console.log("message ? ", remoteMessage);
      emit(remoteMessage);
    });
    return () => {
      messageListener();
    };
  }, buffers.expanding(1));

  try {
    for (;;) {
      const remoteMessage = yield take(onMessageChannel)
      console.log("New Firebase message: ", remoteMessage)
      if (remoteMessage.notification && remoteMessage.notification.title && remoteMessage.notification.body){
        yield call(firebaseService.wrappedAlert, remoteMessage.notification.title, remoteMessage.notification.body, "Ok", false)
      }else {
        console.log("New Firebase data message: ", remoteMessage)
      }
    }
  } finally {
    if (yield cancelled()) {
      onMessageChannel.close()
    }
  }
}

